@import './variables.scss';

@mixin text ($fontFamily,$fontSize,$fontWeight,$lineHeight) {
  font-family: $fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  line-height: $lineHeight;
  color:$textColor
}
@mixin flex ($flex,$contentflex,$alignflex) {
  display: $flex;
  justify-content: $contentflex;
  align-items: $alignflex;
}